import React, { PureComponent } from 'react'
import {
  message,
  Button,
  Row,
  Col
} from 'antd'
import style from './style/Tool.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'

@connect(
  state => ({ ...state.excLog }),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  // 执行异常
  handleCarried = () => {
    const { selectedRowKeys, handleExcLog, searchParams, handleGetList, size, update } = this.props
    let req = []
    selectedRowKeys.map((o) => {
      req.push({
        id: o
      })
    })
    handleExcLog({ data: req }, (data) => {
      message.info('执行' + data.result)
      handleGetList({ searchParams: { ...searchParams, page: 1, size } })
    }, () => {
      update({ tableLoading: false, })
    })
  }
  render() {
    return (
      <div className={style['table-tool']}>
        <Row type="flex" justify="start">
          <Col span={12}>
            <Button type="primary" className={style.btn} onClick={this.handleCarried}>执行</Button>
          </Col>
        </Row>
      </div>
    )
  }
}



