import * as actionType from './actionType'
import {getData} from 'util'
import moment from 'moment'
import {API_EXCEPTIONORDERS_PAGE, API_EXCUSEEXCEPTIONORDERS_SEARCH} from 'api'
//同步
export const update = (object) => ({
  type: actionType.EXCLOG_UPDATE,
  data: object,
})

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({type: actionType.EXCLOG_UPDATE, data: {tableLoading: true}})
  let {page, size} = reqData.searchParams
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  if (reqObj.initDate && reqObj.initDate.length) {
    reqObj.createTimeStart = moment(reqObj.initDate[0]).format('YYYY-MM-DD')
    reqObj.createTimeEnd = moment(reqObj.initDate[1]).format('YYYY-MM-DD')
  }
  delete reqObj.initDate
  // if (!reqObj.warehouseCode) {
  //   reqObj.warehouseCode = reqData.userLoation && reqData.userLoation.value
  // }
  getData({api: API_EXCEPTIONORDERS_PAGE, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.EXCLOG_UPDATE,
      data: {
        page,
        size,
        tableLoading: false,
        total: data.result.total,
        selectedRowKeys: [], // 选中的队列key
        selectedRows: [], // 选中的队列数据
        searchResult: data.result.list || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.EXCLOG_FILE
    })
  })
}

// 执行异常日志
export const handleExcLog = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.EXCLOG_UPDATE, data: {modalTuttonLoading: true, tableLoading: true,}})
  let api = API_EXCUSEEXCEPTIONORDERS_SEARCH
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.EXCLOG_UPDATE,
      data: {
        modalTuttonLoading: false,
        isShowModal: false,
        dataNum: data.result
      }
    })
    callBack&&callBack(data)
  }).catch(err => {
    dispatch({
      type: actionType.EXCLOG_FILE
    })
  })
}


