import React, { PureComponent } from 'react'
import {
  Table,
} from 'antd'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'

@connect(
  state => ({ ...state.excLog, userLoation: state.userLoation }),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '任务编号',
      width: 150,
      dataIndex: 'taskCode',
      key: 'taskCode',
    }, {
      title: '来源单',
      width: 150,
      dataIndex: 'originOrderCode',
      key: 'originOrderCode'
    }, {
      title: '仓库',
      width: 200,
      dataIndex: 'warehouseName',
      key: 'warehouseName'
    }, {
      title: '接口编号',
      width: 200,
      dataIndex: 'interfaceName',
      key: 'interfaceName'
    }, {
      title: '备注',
      width: 250,
      dataIndex: 'remark',
      key: 'remark'
    }, {
      title: '状态',
      width: 150,
      dataIndex: 'statusDesc',
      key: 'statusDesc'
    }, {
      title: '创建时间',
      width: 150,
      dataIndex: 'createTime',
      key: 'createTime'
    }, {
      title: '执行成功时间',
      width: 150,
      dataIndex: 'excuseSuccessTime',
      key: 'excuseSuccessTime'
    }]
  }

  componentDidMount() {
    const { handleGetList, searchParams, page, size, userLoation } = this.props
    // if (!userLoation || !userLoation.value) return
    handleGetList({ searchParams: { page, size, ...searchParams }, userLoation })
  }

  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const { handleGetList, searchParams, page, size, userLoation } = this.props
    if (newProps.userLoation.value != userLoation.value) {
      handleGetList({ searchParams: { page: 1, size: newProps.size, ...newProps.searchParams } })
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const { handleGetList, searchParams, page, size } = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({ searchParams: { page, size, ...searchParams, isAsc: true, columns: sorter.field } })
      } else {
        handleGetList({ searchParams: { page, size, ...searchParams, isAsc: false, columns: sorter.field } })
      }
    } else {
      // 判断是点跳页面还是排序
      handleGetList({ searchParams: { page: pagination.current, size: pagination.pageSize, ...searchParams } })
    }
  }

  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      searchResult,
      selectedRowKeys
    } = this.props
    
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['50', '100', '200', '500']
    }
    // rowSelection object indicates the need for row selection
    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        const { update } = this.props
        update({ selectedRowKeys })
      },
      selectedRowKeys,
    };
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          rowSelection={rowSelection}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{ x: 'max-content', y: 400 }}
          onChange={this.onChange}
          pagination={pagination}
        />
      </div>
    )
  }
}



