import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  Input,
  message,
  DatePicker
} from 'antd'
import style from './style/Form.scss'
import {CsxSelect} from 'Form'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {API_USER_WAREHOUSELIST} from 'api'
import moment from 'moment'
const {RangePicker} = DatePicker
const statusOptions = [
  {key: '0', value: '失败'},
  {key: '1', value: '成功'}
]
@connect(
  state => ({...state.excLog, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  componentWillMount() {
    this.initDateOpt()
  }
  // 设置默认的时间  前一周 - 当天
  initDateOpt(values) {
    const {update} = this.props
    const preMonth = moment().subtract(1, 'M').startOf('day')
    update({searchParams: {initDate: [preMonth, moment().endOf('day')]}})
  }

  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }

  // 查询数据
  query = () => {
    const {handleGetList, searchParams, size, userLoation} = this.props
    let reqObj = {...searchParams}
    handleGetList({searchParams: {...reqObj, page: 1, size}, userLoation})
  }

  // 新增
  handleAdd = () => {
    const {update} = this.props
    update({isShowModal: true, modalTitle: '新增货主', modalData: ''})
  }
  // 重置
  reset = () => {
    const {update} = this.props
    update({searchParams: {}})
    this.initDateOpt()
  }
  render() {
    const {
      searchParams: {
        originOrderCode,
        interfaceName,
        initDate,
        status,
        remark = '',
        warehouseCode
      },
      total
    } = this.props
    let dataLen = `${total}条`
    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={8}>
            <div className='commonInput'>
              <label>来源单查询：</label>
              <div className='box'>
                <Input value={originOrderCode} onChange={(e) => {
                  this.onInputChange({originOrderCode: e.target.value})
                }} />
              </div>
            </div>
          </Col>
          <Col span={8}>
            <div className='commonInput'>
              <label>接口：</label>
              <div className='box'>
                <Input value={interfaceName} onChange={(e) => {
                  this.onInputChange({interfaceName: e.target.value})
                }} />
              </div>
            </div>
          </Col>
          <Col span={8}>
            <CsxSelect
              dateApi={API_USER_WAREHOUSELIST}
              label='仓库'
              selectItem={{data: warehouseCode, key: 'warehouseCode', valueKey: ['warehouseCode', 'warehouseName']}}
              onChangeSelect={(value) => {
                this.onInputChange({warehouseCode: value})
              }}
            />
          </Col>
          <Col span={8}>
            <div className='commonInput'>
              <label>创建时间：</label>
              <div className='box' style={{marginRight: 0}}>
                <RangePicker onChange={(moment, string) => {
                  this.onInputChange({initDate: moment})
                }}
                  format={'YYYY-MM-DD'}
                  value={initDate}
                  allowClear={true}
                />
              </div>
            </div>
          </Col>
          <Col span={8}>
            <CsxSelect
              data={statusOptions}
              label='状态'
              selectItem={{data: status, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({status: value})
              }}
            />
          </Col>
          <Col span={8}>
            <div className='commonInput'>
              <label>备注：</label>
              <div className='box'>
                <Input value={remark} onChange={(e) => {
                  this.onInputChange({remark: e.target.value})
                }} />
              </div>
            </div>
          </Col>
          <Col span={8} push={16} >
            <Row type="flex" justify="end" >
              <Button type="primary" className={style.mr10} onClick={this.query} >查询</Button>
              <Button type="primary" className={style.mr10} onClick={this.reset} >重置</Button>
            </Row>
          </Col>
          <Col span={24}>
            <Row type="flex" justify="end" >
              <h3 style={{marginTop: 24, marginRight: 10}} ><span>查询结果：</span><strong>{dataLen}</strong></h3>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



